import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button, Dropdown, Form, Row, Col } from "react-bootstrap";
import { Context } from "../../index";
import {
  createProduct,
  fetchGroups,
  fetchTypes,
  fetchViews,
} from "../../http/productAPI";
import { observer } from "mobx-react-lite";

const CreateProduct = observer(({ show, onHide }) => {
  const { product } = useContext(Context);
  const [name, setName] = useState("");
  const [price, setPrice] = useState(0);
  const [file, setFile] = useState(null);
  const [info, setInfo] = useState([]);
  const [views, setViews] = useState([]);

  useEffect(() => {
    let isMounted = true; // ,  ,   

    const fetchData = async () => {
      try {
        const typesData = await fetchTypes();
        const groupsData = await fetchGroups();
        const viewsData = await fetchViews();

        if (isMounted) {
          // ,   ,   
          product.setTypes(typesData);
          product.setGroups(groupsData);
          setViews(viewsData); //     state
        }
      } catch (error) {
        console.error("   :", error);
      }
    };

    fetchData(); //   

    return () => {
      isMounted = false; //    false  
    };
  }, []);
  const addInfo = () => {
    setInfo([...info, { title: "", description: "", number: Date.now() }]);
  };

  const removeInfo = (number) => {
    setInfo(info.filter((i) => i.number !== number));
  };

  const changeInfo = (key, value, number) => {
    setInfo(
      info.map((i) => (i.number === number ? { ...i, [key]: value } : i))
    );
  };

  const selectFile = (e) => {
    setFile(e.target.files[0]);
  };

  const addProduct = () => {
    // Ensure the required fields are selected
    if (
      !product.selectedGroup ||
      !product.selectedType ||
      !product.selectedView
    ) {
      alert(" ,      ");
      return;
    }

    const formData = new FormData();
    formData.append("name", name);
    formData.append("price", `${price}`);
    formData.append("img", file);
    formData.append("viewId", product.selectedView.id);
    formData.append("groupId", product.selectedGroup.id);
    formData.append("typeId", product.selectedType.id);
    formData.append("info", JSON.stringify(info));

    createProduct(formData).then(() => {
      onHide(); // Close the modal after successful creation
    });
  };

  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
           
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          {/* Dropdown for selecting view */}
          <Dropdown className="mt-2 mb-2">
            <Dropdown.Toggle>
              {product.selectedView?.name || " "}
            </Dropdown.Toggle>
            <Dropdown.Menu>
              {product.views.map((view) => (
                <Dropdown.Item
                  onClick={() => product.setSelectedView(view)} // Ensure method exists
                  key={view.id}
                >
                  {view.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>

          {/* Dropdown for selecting type */}
          <Dropdown className="mt-2 mb-2">
            <Dropdown.Toggle>
              {product.selectedType?.name || " "}
            </Dropdown.Toggle>
            <Dropdown.Menu>
              {product.types.map((type) => (
                <Dropdown.Item
                  onClick={() => product.setSelectedType(type)} // Ensure method exists
                  key={type.id}
                >
                  {type.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>

          {/* Dropdown for selecting group */}
          <Dropdown className="mt-2 mb-2">
            <Dropdown.Toggle>
              {product.selectedGroup?.name || " "}
            </Dropdown.Toggle>
            <Dropdown.Menu>
              {product.groups.map((group) => (
                <Dropdown.Item
                  onClick={() => product.setSelectedGroup(group)} // Ensure method exists
                  key={group.id}
                >
                  {group.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>

          {/* Product name input */}
          <Form.Control
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="mt-3"
            placeholder="  "
          />

          {/* Price input */}
          <Form.Control
            value={price}
            onChange={(e) => setPrice(Number(e.target.value))}
            className="mt-3"
            placeholder="  "
            type="number"
          />

          {/* File input */}
          <Form.Control className="mt-3" type="file" onChange={selectFile} />

          <hr />

          {/* Button to add product info */}
          <Button variant={"outline-dark"} onClick={addInfo}>
              
          </Button>

          {/* Dynamically added product info fields */}
          {info.map((i) => (
            <Row className="mt-4" key={i.number}>
              <Col md={4}>
                <Form.Control
                  value={i.title}
                  onChange={(e) =>
                    changeInfo("title", e.target.value, i.number)
                  }
                  placeholder="  "
                />
              </Col>
              <Col md={4}>
                <Form.Control
                  value={i.description}
                  onChange={(e) =>
                    changeInfo("description", e.target.value, i.number)
                  }
                  placeholder="  "
                />
              </Col>
              <Col md={4}>
                <Button
                  onClick={() => removeInfo(i.number)}
                  variant={"outline-danger"}
                >
                  
                </Button>
              </Col>
            </Row>
          ))}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          
        </Button>
        <Button variant="outline-success" onClick={addProduct}>
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
});

export default CreateProduct;
