import React, { useState, useEffect, useContext } from "react";
import { Button, Container } from "react-bootstrap";
import CreateGroup from "../components/modals/CreateGroup";
import CreateProduct from "../components/modals/CreateProduct";
import CreateType from "../components/modals/CreateType";
import CreateView from "../components/modals/CreateView";
import { fetchViews, fetchGroups, fetchTypes } from "../http/productAPI";
import { Context } from "../index";

const Admin = () => {
  const [groupVisible, setGroupVisible] = useState(false);
  const [typeVisible, setTypeVisible] = useState(false);
  const [productVisible, setProductVisible] = useState(false);
  const [viewVisible, setViewVisible] = useState(false);

  const { product } = useContext(Context);

  useEffect(() => {
    fetchViews()
      .then((data) => {
        product.setViews(data);
        console.log(" Views fetched:", data);
      })
      .catch((error) => console.error(" Error fetching views:", error));

    fetchGroups()
      .then((data) => {
        product.setGroups(data);
        console.log(" Groups fetched:", data);
      })
      .catch((error) => console.error(" Error fetching groups:", error));

    fetchTypes()
      .then((data) => {
        product.setTypes(data);
        console.log(" Types fetched:", data);
      })
      .catch((error) => console.error(" Error fetching types:", error));
  }, [product]);

  useEffect(() => {
    console.log(" :");
    console.log("   :", viewVisible);
    console.log("   :", groupVisible);
    console.log("   :", typeVisible);
    console.log("   :", productVisible);
    console.log("Views:", product.views);
    console.log("Groups:", product.groups);
  }, [
    viewVisible,
    groupVisible,
    typeVisible,
    productVisible,
    product.views,
    product.groups,
  ]);

  return (
    <Container className="d-flex flex-column">
      <Button
        variant={"outline-dark"}
        className="mt-4 p-2"
        onClick={() => setViewVisible(true)}
      >
         
      </Button>
      <Button
        variant={"outline-dark"}
        className="mt-4 p-2"
        onClick={() => setGroupVisible(true)}
      >
         
      </Button>
      <Button
        variant={"outline-dark"}
        className="mt-4 p-2"
        onClick={() => setTypeVisible(true)}
      >
         
      </Button>
      <Button
        variant={"outline-dark"}
        className="mt-4 p-2"
        onClick={() => setProductVisible(true)}
      >
         
      </Button>

      <CreateView show={viewVisible} onHide={() => setViewVisible(false)} />
      <CreateGroup show={groupVisible} onHide={() => setGroupVisible(false)} />
      <CreateProduct
        show={productVisible}
        onHide={() => setProductVisible(false)}
      />
      <CreateType show={typeVisible} onHide={() => setTypeVisible(false)} />
    </Container>
  );
};

export default Admin;
