import React, { useContext } from "react";
import { observer } from "mobx-react-lite";
import { Context } from "../index"; //    ,    
import Button from "../components/ui/Button";

const CartPage = observer(() => {
  const { product } = useContext(Context);
  const cart = product.cart;

  const handleRemove = (id) => {
    product.removeFromCart(id);
  };

  const handleClearCart = () => {
    cart.forEach((item) => product.removeFromCart(item.id));
  };

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">  </h1>

      {cart.length === 0 ? (
        <div className="text-gray-500 text-lg"> .</div>
      ) : (
        <>
          <div className="flex justify-end mb-4">
            <Button variant="outline" onClick={handleClearCart}>
               
            </Button>
          </div>

          <div className="space-y-4">
            {cart.map((item) => (
              <div
                key={item.id}
                className="bg-white shadow rounded-2xl p-4 flex justify-between items-center"
              >
                <div>
                  <h2 className="text-lg font-semibold">{item.name}</h2>
                  {item.view && (
                    <p className="text-sm text-gray-500">: {item.view}</p>
                  )}
                  {item.group && (
                    <p className="text-sm text-gray-500">
                      : {item.group}
                    </p>
                  )}
                  {item.type && (
                    <p className="text-sm text-gray-500">: {item.type}</p>
                  )}
                  <div className="mt-1 text-sm text-gray-700">
                    {item.width && <p>: {item.width}</p>}
                    {item.length && <p>: {item.length}</p>}
                    {item.height && <p>: {item.height}</p>}
                    {item.diameter && <p>: {item.diameter}</p>}
                    {item.threadType && <p>: {item.threadType}</p>}
                  </div>
                  <p className="text-sm text-gray-500 mt-1">
                    -: {item.quantity}
                  </p>
                </div>

                <Button
                  variant="destructive"
                  onClick={() => handleRemove(item.id)}
                >
                  
                </Button>
              </div>
            ))}
          </div>
        </>
      )}
    </div>
  );
});

export default CartPage;
