import React, { useContext, useEffect } from "react";
import { Container } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import NavBar2 from "../components/NavBar2";
import TypeBar from "../components/TypeBar";
import ViewBar from "../components/ViewBar";
import GroupBar from "../components/GroupBar";
import ProductList from "../components/ProductList";
import { observer } from "mobx-react-lite";
import { Context } from "../index";
import {
  fetchGroups,
  fetchProducts,
  fetchTypes,
  fetchViews, //   
} from "../http/productAPI";
import Pages from "../components/Pages";

const Shop = observer(() => {
  const { product } = useContext(Context);

  useEffect(() => {
    fetchTypes().then((data) => product.setTypes(data));
    fetchGroups().then((data) => product.setGroups(data));
    fetchViews().then((data) => product.setViews(data)); //    views

    fetchProducts(null, null, 1, 2).then((data) => {
      product.setProducts(data.rows);
      product.setTotalCount(data.count);
    });
  }, []);

  useEffect(() => {
    const typeId = product.selectedType?.id || null;
    const groupId = product.selectedGroup?.id || null;
    const viewId = product.selectedView?.id || null;

    const page = product.page;

    fetchProducts(typeId, groupId, viewId, page, 2).then((data) => {
      product.setProducts(data.rows);
      product.setTotalCount(data.count);
    });
  }, [
    product.page,
    product.selectedType?.id,
    product.selectedGroup?.id,
    product.selectedView?.id,
  ]);

  return (
    <>
      <NavBar2 />
      <Container>
        <Row className="mt-2">
          <Col md={3}>
            <TypeBar />
          </Col>
          <Col md={3}>
            <ViewBar />
          </Col>
          <Col md={9}>
            <GroupBar />
            <ProductList />
            <Pages />
          </Col>
        </Row>
      </Container>
    </>
  );
});

export default Shop;
